import { combineReducers } from 'redux';
import { ADD, DELETE, FILTER } from './action-types';


const initialState = {
  contacts: {
      items: [
          {id: 'id-1', name: 'Rosie Simpson', number: '459-12-56'},
          {id: 'id-2', name: 'Hermione Kline', number: '443-89-12'},
          {id: 'id-3', name: 'Eden Clements', number: '645-17-79'},
          {id: 'id-4', name: 'Annie Copeland', number: '227-91-26'},
      ],
      filter: '',
  }
}

const filterReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case FILTER:
      return payload;

    default:
      return state;
  }
};

const contactsReducer = (state = initialState.contacts.items, { type, payload }) => {
  switch (type) {
    case ADD:
      if (state.find(({ name }) => name === payload.name)) {
        alert(`${payload.name} is already exists`);
        return [...state];
      }
      return [...state, payload];

    case DELETE:
      return state.filter(({ id }) => id !== payload);
       
    default:
      return state;
  }
};

const combineReducer = combineReducers({
  contacts: contactsReducer,
  filter: filterReducer,
})



export { combineReducer }